import { Link } from "gatsby"
import PropTypes from "prop-types"
import React, { useState } from "react"
import DEFAULT_TILE_IMG from '../images/jeremy/portrait-side.jpg'
import './tiles.css'

const Tiles = ({ data = [], limit }) => {

  let usedData

  if (limit != null) {
    usedData = data.slice(0, limit)
  } else {
    usedData = data.slice(0)
  }

  return (
    <div>
      <Link to="/blog"><h2 className="portfolio-heading">Blog</h2></Link>
      <section className={`portfolio-grid col-3'}`}>
        {usedData.map(({ node }) => {
          const { cover } = node.frontmatter
          let { publicURL } = cover || {}
          if (publicURL == null) {
            publicURL = DEFAULT_TILE_IMG
          }

          return (
            <Link key={node.fields.slug} to={node.fields.slug} className="portfolio-project">
              <div
                className="project-pic"
                style={publicURL ? {"backgroundImage": `url(${publicURL})`} : {}}
              ></div>
              <h2>{node.frontmatter.title}</h2>
              <section>{node.excerpt}</section>
              {/*<footer>Tags</footer>*/}
            </Link>
          )
        })}
      </section>
    </div>
)}

Tiles.propTypes = {
}

Tiles.defaultProps = {
}

export default Tiles
